import { Component, Prop, Event, EventEmitter, h, State } from '@stencil/core';

export type PaginationSize = 'sm' | 'md' | 'lg';

export interface PaginationChangeEvent {
  page: number;
  pageSize: number;
}

@Component({
  tag: 'bh-pagination',
  styleUrl: 'bh-pagination.css',
  shadow: true,
})
export class BhPagination {
  /**
   * Current page (1-indexed)
   */
  @Prop({ mutable: true }) page: number = 1;

  /**
   * Total number of items
   */
  @Prop() totalItems: number = 0;

  /**
   * Number of items per page
   */
  @Prop({ mutable: true }) pageSize: number = 10;

  /**
   * Available page size options
   */
  @Prop() pageSizeOptions: number[] = [10, 25, 50, 100];

  /**
   * Show page size selector
   */
  @Prop() showPageSizeSelector: boolean = true;

  /**
   * Show item count info (e.g., "Showing 1 to 10 of 100 results")
   */
  @Prop() showItemCount: boolean = true;

  /**
   * Show first/last page buttons
   */
  @Prop() showFirstLastButtons: boolean = true;

  /**
   * Size variant of the pagination
   */
  @Prop() size: PaginationSize = 'md';

  /**
   * Whether the pagination is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Label for "Rows per page" text (for i18n)
   */
  @Prop() rowsPerPageLabel: string = 'Rows per page:';

  /**
   * Label template for showing results (use {start}, {end}, {total} placeholders)
   */
  @Prop() showingResultsLabel: string = 'Showing {start} to {end} of {total} results';

  /**
   * Label template for page info when total items unknown (use {page}, {totalPages} placeholders)
   */
  @Prop() pageInfoLabel: string = 'Page {page} of {totalPages}';

  @State() internalPage: number = 1;
  @State() internalPageSize: number = 10;

  /**
   * Emitted when the page changes
   */
  @Event() bhPageChange!: EventEmitter<PaginationChangeEvent>;

  /**
   * Emitted when page size changes
   */
  @Event() bhPageSizeChange!: EventEmitter<PaginationChangeEvent>;

  componentWillLoad() {
    this.internalPage = this.page;
    this.internalPageSize = this.pageSize;
  }

  private get totalPages(): number {
    return Math.max(1, Math.ceil(this.totalItems / this.internalPageSize));
  }

  private get startItem(): number {
    if (this.totalItems === 0) return 0;
    return (this.internalPage - 1) * this.internalPageSize + 1;
  }

  private get endItem(): number {
    return Math.min(this.internalPage * this.internalPageSize, this.totalItems);
  }

  private get canGoPrevious(): boolean {
    return this.internalPage > 1 && !this.disabled;
  }

  private get canGoNext(): boolean {
    return this.internalPage < this.totalPages && !this.disabled;
  }

  private goToPage(newPage: number) {
    if (this.disabled) return;

    const page = Math.max(1, Math.min(newPage, this.totalPages));
    if (page !== this.internalPage) {
      this.internalPage = page;
      this.page = page;
      this.bhPageChange.emit({ page, pageSize: this.internalPageSize });
    }
  }

  private handlePageSizeChange(event: Event) {
    if (this.disabled) return;

    const select = event.target as HTMLSelectElement;
    const newPageSize = parseInt(select.value, 10);

    if (newPageSize !== this.internalPageSize) {
      this.internalPageSize = newPageSize;
      this.pageSize = newPageSize;

      // Reset to page 1 when page size changes, or adjust if current page is now out of bounds
      const newTotalPages = Math.max(1, Math.ceil(this.totalItems / newPageSize));
      const newPage = Math.min(this.internalPage, newTotalPages);

      if (newPage !== this.internalPage) {
        this.internalPage = newPage;
        this.page = newPage;
      }

      this.bhPageSizeChange.emit({ page: this.internalPage, pageSize: newPageSize });
    }
  }

  private goToFirst = () => this.goToPage(1);
  private goToPrevious = () => this.goToPage(this.internalPage - 1);
  private goToNext = () => this.goToPage(this.internalPage + 1);
  private goToLast = () => this.goToPage(this.totalPages);

  private getInfoText(): string {
    if (this.totalItems === 0) {
      return 'No results';
    }

    if (this.showItemCount && this.totalItems > 0) {
      return this.showingResultsLabel
        .replace('{start}', String(this.startItem))
        .replace('{end}', String(this.endItem))
        .replace('{total}', String(this.totalItems));
    }

    return this.pageInfoLabel
      .replace('{page}', String(this.internalPage))
      .replace('{totalPages}', String(this.totalPages));
  }

  render() {
    const paginationClasses = {
      'pagination': true,
      [`pagination-${this.size}`]: true,
      'pagination-disabled': this.disabled,
    };

    return (
      <div class={paginationClasses} role="navigation" aria-label="Pagination">
        {/* Info section */}
        <div class="pagination-info">
          <span class="pagination-info-text">{this.getInfoText()}</span>
        </div>

        {/* Controls section */}
        <div class="pagination-controls">
          {/* Page size selector */}
          {this.showPageSizeSelector && (
            <div class="pagination-page-size">
              <span class="pagination-page-size-label">{this.rowsPerPageLabel}</span>
              <select
                class="pagination-select"
                onChange={(e) => this.handlePageSizeChange(e)}
                disabled={this.disabled}
                aria-label="Select page size"
              >
                {this.pageSizeOptions.map((size) => (
                  <option key={size} value={String(size)} selected={size === this.internalPageSize}>
                    {size}
                  </option>
                ))}
              </select>
            </div>
          )}

          {/* Navigation buttons */}
          <div class="pagination-nav">
            {this.showFirstLastButtons && (
              <button
                type="button"
                class="pagination-btn"
                onClick={this.goToFirst}
                disabled={!this.canGoPrevious}
                aria-label="Go to first page"
              >
                <span class="material-symbols-outlined">first_page</span>
              </button>
            )}

            <button
              type="button"
              class="pagination-btn"
              onClick={this.goToPrevious}
              disabled={!this.canGoPrevious}
              aria-label="Go to previous page"
            >
              <span class="material-symbols-outlined">chevron_left</span>
            </button>

            <button
              type="button"
              class="pagination-btn"
              onClick={this.goToNext}
              disabled={!this.canGoNext}
              aria-label="Go to next page"
            >
              <span class="material-symbols-outlined">chevron_right</span>
            </button>

            {this.showFirstLastButtons && (
              <button
                type="button"
                class="pagination-btn"
                onClick={this.goToLast}
                disabled={!this.canGoNext}
                aria-label="Go to last page"
              >
                <span class="material-symbols-outlined">last_page</span>
              </button>
            )}
          </div>
        </div>
      </div>
    );
  }
}
